import React,{ Component } from 'react';
import firebase from './../../firebase';

import $ from 'jquery';
import Likes from './likes';

export default class Home extends Component {
  constructor(props) {
      super(props);
      this.state = {
        allPosts: [],
        countPosts: 0,
        allCategory: [],
        allUsers: [],
        currentPage: 1,
        itemsPerPage: 5,
        pageNumberLimit: 5,
        maxPageNumberLimit: 5,
        minPageNumberLimit: 0,
        pages: [],
        indexOfLastItem: 0,
        indexOfFirstItem: 0,
        currentItems: []
      }
      this.select = React.createRef();
      this.categ = '';
      this.userOfComment = '';
      this.userOfCommentEmail = '';
    }

  componentDidMount() {
    if(!localStorage.getItem("backgroundColor")){
      localStorage.setItem("backgroundColor",(localStorage.getItem('backgroundColor')== null)?"#282c34":localStorage.getItem("backgroundColor"));
    }
    if(!localStorage.getItem("fontColor")){
      localStorage.setItem("fontColor","#fff");
    }
    if(!localStorage.getItem("navBarColor")){
      localStorage.setItem("navBarColor","bg-dark navbar-dark");
    }
    //alert("Show Posts");
    firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          snapshot.forEach(child => {
          var data = child.val();
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,category_id: data.category_id,image: data.image,time: data.time});
          });
          //setAllPosts(posts);
          //setCountPosts(posts.length);
          //this.setState({ channels: data })
          var firstind = this.state.indexOfFirstItem;
          firstind = firstind<0?0:firstind;
          var lastind = this.state.indexOfLastItem;
          //console.log(firstind);
          //console.log(lastind);
          //console.log(posts.slice(0,5));
          //console.log(posts.slice(firstind, lastind));
          this.setState({ 
            allPosts: posts,
            countPosts: posts.length,
            currentItems: posts.slice(firstind, lastind)
           });

           var pages = [];
          for (let i = 1; i <= Math.ceil(this.state.allPosts.length / this.state.itemsPerPage); i++) {
            pages.push(i);
          }
          this.setState({ 
            pages: pages
          });
          console.log(this.state.pages);
      });
      firebase.database().ref("/category").orderByChild('id')
      .on('value', snapshot => {
          let category = [];
          snapshot.forEach(child => {
          var data = child.val();
          category.push({id: data.id,title: data.title,description: data.description,time: data.time});
          });
          this.setState({ 
            allCategory: category
           });
      });
      firebase.database().ref("/users").orderByChild('id')
        .on('value', snapshot => {
            let users = [];
            snapshot.forEach(child => {
            var data = child.val();
            users.push({id: data.id,email: data.email,name: data.name});
            });
            this.setState({ 
                allUsers: users
            });
        });
      $(".container-fluid").css("position","relative");
      $("body").css("background-color",(localStorage.getItem('backgroundColor')== null)?"#282c34":localStorage.getItem("backgroundColor"));
      this.setState({
        indexOfLastItem: this.state.currentPage * this.state.itemsPerPage,
        indexOfFirstItem: this.state.indexOfLastItem - this.state.itemsPerPage
      });
  }

  handleClick(event){
    this.setState({ 
      currentPage: Number(event.target.id)
    });
    this.setState({
      currentItems: this.state.allPosts.slice(this.state.itemsPerPage*this.state.currentPage, this.state.itemsPerPage*(this.state.currentPage + 1))
    });
  }

  handleNextbtn(){
    this.setState({ 
      currentPage: (this.state.currentPage + 1)
     });

    if (this.state.currentPage + 1 > this.state.maxPageNumberLimit) {
      this.setState({ 
        maxPageNumberLimit: (this.state.maxPageNumberLimit + this.state.pageNumberLimit),
        minPageNumberLimit: (this.state.minPageNumberLimit + this.state.pageNumberLimit)
       });
    }
    this.setState({
      currentItems: this.state.allPosts.slice(this.state.itemsPerPage*this.state.currentPage, this.state.itemsPerPage*(this.state.currentPage + 1))
    });
  }

  handlePrevbtn(){
    this.setState({ 
      currentPage: (this.state.currentPage - 1)
     });

    if ((this.state.currentPage - 1) % this.state.pageNumberLimit == 0) {
      this.setState({ 
        maxPageNumberLimit: (this.state.maxPageNumberLimit - this.state.pageNumberLimit),
        minPageNumberLimit: (this.state.minPageNumberLimit - this.state.pageNumberLimit)
       });
    }
    var indexmin = (this.state.itemsPerPage*(this.state.currentPage - 2))<0?0:(this.state.itemsPerPage*(this.state.currentPage - 2));
    var indexmax = this.state.itemsPerPage*(this.state.currentPage - 1);
    console.log(indexmin);
    console.log(indexmax);
    this.setState({
      currentItems: this.state.allPosts.slice(indexmin, indexmax)
    });
  }

  handleLoadMore(){
    this.setState({ 
      itemsPerPage: (this.state.itemsPerPage + 5)
     });
     this.setState({
      currentItems: this.state.allPosts.slice(this.state.itemsPerPage*(this.state.currentPage - 1), this.state.itemsPerPage*(this.state.currentPage + 1))
    });
  }

  moredetails(id){
    //alert("Mode details"+id);
    window.location.href = "/reactblog/#/viewpost/"+id;
  }

  selectchange(){
    //alert("Select Change"+this.select.current.value);
    if(this.select.current.value == 1){
      firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          snapshot.forEach(child => {
          var data = child.val();
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,category_id: data.category_id,image: data.image,time: data.time});
          });
          this.setState({ 
            allPosts: posts,
            countPosts: posts.length
           });
      });
    } else{
      firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          snapshot.forEach(child => {
          var data = child.val();
          if(data.category_id == this.select.current.value ){
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,category_id: data.category_id,image: data.image,time: data.time});
          }
          });
          this.setState({ 
            allPosts: posts,
            countPosts: posts.length
           });
      });
    }
  }

  render(){
    const bgstyle={
      backgroundColor: (localStorage.getItem('backgroundColor')== null)?"#282c34":localStorage.getItem("backgroundColor"),
      color: 'white'
    }
    
    const carousel={
      marginBottom: '50px'
    }
  
    const posts={
      marginBottom: '50px',
      color: 'black'
    }
  
    const allposts={
      marginBottom: '50px'
    }

    const categ={
      marginLeft: '0',
      marginRight: '0',
      paddingLeft: '0',
      paddingRinght: '0',
      marginBottom: '20px'
    }

    const imgst={
      marginBottom: '10px'
    }

    const pst={
      marginTop: '-10px'
    }

    const pfilterst={
      paddingRight: '0'
    }

    const hrst={
      display: 'block',
      height: '1px',
      border: '0',
      borderTop: '1px solid #000',
      margin: '1em 0',
      padding: '0'
    }

    const renderPageNumbers = this.state.pages.map((number) => {
      if (number < this.state.maxPageNumberLimit + 1 && number > this.state.minPageNumberLimit) {
        return (
          <li
            key={number}
            id={number}
            onClick={(event)=>this.handleClick(event)}
            className={this.state.currentPage == number ? "active" : null}
          >
            {number}
          </li>
        );
      } else {
        return null;
      }
    });
  
    let pageIncrementBtn = null;
    if (this.state.pages.length > this.state.maxPageNumberLimit) {
      pageIncrementBtn = <li onClick={()=>this.handleNextbtn()}> &hellip; </li>;
    }
  
    let pageDecrementBtn = null;
    if (this.state.minPageNumberLimit >= 1) {
      pageDecrementBtn = <li onClick={()=>this.handlePrevbtn()}> &hellip; </li>;
    }
    console.log(this.state.currentItems);
    
  return (
      <div className="container" style={bgstyle}>
        <h1>Home</h1>

        <div id="demo" className="carousel slide" data-ride="carousel" style={carousel}>
            <ul className="carousel-indicators">
                <li data-target="#demo" data-slide-to="0" className="active"></li>
                <li data-target="#demo" data-slide-to="1"></li>
                <li data-target="#demo" data-slide-to="2"></li>
            </ul>
            <div className="carousel-inner">
                <div className="carousel-item active">
                <img src="https://www.w3schools.com/bootstrap4/la.jpg" alt="Los Angeles"/>
                </div>
                <div className="carousel-item">
                <img src="https://www.w3schools.com/bootstrap4/chicago.jpg" alt="Chicago"/>
                </div>
                <div className="carousel-item">
                <img src="https://www.w3schools.com/bootstrap4/ny.jpg" alt="New York"/>
                </div>
            </div>
            <a className="carousel-control-prev" href="#demo" data-slide="prev">
                <span className="carousel-control-prev-icon"></span>
            </a>
            <a className="carousel-control-next" href="#demo" data-slide="next">
                <span className="carousel-control-next-icon"></span>
            </a>
        </div>
        <div className="col-9 col-sm-9 col-md-9 col-lg-9 col-xl-9 mx-auto" style={categ}>
          <p className="text-left col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12" style={pfilterst}><strong>Total Posts: {this.state.countPosts}</strong>
          <select className="custom-select" ref={this.select} onChange={()=>this.selectchange()}>
          {this.state.allCategory.map((item, index) => (<option value={item.id} key={item.id}>{item.title}</option>))}
          </select>
          </p>
          </div>
        <div className="col-9 col-sm-9 col-md-9 col-lg-9 col-xl-9 mx-auto" style={allposts}>
        {this.state.currentItems.map((item, index) => (<div className="card mx-auto" style={posts}>
          <div className="card-body">
          <img className="img-fluid" src={"https://firebasestorage.googleapis.com/v0/b/reactblog-824c0.appspot.com/o/image%2F"+item.image+"?alt=media"} style={imgst}/>
          <p className="text-left"><strong><i>{item.title}</i></strong></p>
          <p className="text-left" style={pst}><strong><i>{item.description}</i></strong></p>
          {this.state.allCategory.forEach((itemcateg)=>{
            if(itemcateg.id == item.category_id){
              this.categ = itemcateg.title;
            }
          })}
          <p className="text-left" style={pst}><strong><i>Category: {this.categ}</i></strong></p>
          {this.state.allUsers.forEach((itemUser)=>{
              if(itemUser.email == item.user_id){
              this.userOfComment = itemUser.name;
              this.userOfCommentEmail = item.user_id;
              }
          })}
          <p className="text-left" style={pst}><strong>Author: {(localStorage.getItem('authlogin')=='true' && this.userOfCommentEmail==localStorage.getItem("user_id"))?("Me"):(this.userOfComment+'-'+this.userOfCommentEmail)}:</strong></p>
          <p className="text-left" style={pst}><strong><i>Created at: {item.time}</i></strong></p>
          <hr className="col-12" style={hrst}/>
          <Likes post_id={item.id}/>
          <hr className="col-12" style={hrst}/>
          <button className="btn btn-primary" onClick={()=>this.moredetails(item.id)}>More Details</button></div></div>))}
          
          <div className="row">
            <div className="row mx-auto">
          <ul className="pageNumbers">
          <li>
            <button
              onClick={()=>this.handlePrevbtn()}
              disabled={this.state.currentPage == this.state.pages[0] ? true : false}
            >
              Prev
            </button>
          </li>
          {pageDecrementBtn}
          {renderPageNumbers}
          {pageIncrementBtn}

          <li>
            <button
              onClick={()=>this.handleNextbtn()}
              disabled={this.state.currentPage == this.state.pages[this.state.pages.length - 1] ? true : false}
            >
              Next
            </button>
          </li>
        </ul>
        <button onClick={()=>this.handleLoadMore()} className="loadmore">
          Load More
        </button>
        </div>
        </div>

        </div>
      </div>
    );
  }
}
